}

.fonts-loaded > body {
  font-family: "Eczar", sans-serif;
}

.lte-ie8 > body {
  font-family: "Eczar", sans-serif;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover, a:active, a:focus {
  color: #000;
}

/* Headings
----------------------------------------------------------------------------------------------------*/
h1 {
  font-size: 25px;
}

h2 {
  font-size: 23px;
}

h3 {
  font-size: 21px;
}

h4 {
  font-size: 19px;
}

h5 {
  font-size: 17px;
}

h6 {
  font-size: 15px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
  line-height: 1.2;
}

/* Lists
----------------------------------------------------------------------------------------------------*/
ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
}

/* Spacing
----------------------------------------------------------------------------------------------------*/
p,
dl,
hr,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
pre,
table,
address,
fieldset,
figure {
  margin: 0 0 20px 0;
}

/* Wysiwyg style
----------------------------------------------------------------------------------------------------*/
hr {
  clear: both;
  border: 0;
  height: 1px;
  background: #ddd;
}

blockquote {
  border-left: 5px solid #ddd;
  padding: 20px;
  margin: 0 0 20px 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

blockquote p:last-child {
  margin: 0;
}

cite {
  font-style: italic;
}

small {
  font-size: 11px;
}

table {
  margin: 20px 0;
  width: 100%;
  border: 1px solid #ddd;
  border-collapse: collapse;
  border-left: 0;
  border-radius: 3px;
}

table th {
  padding: 10px;
  border-left: 1px solid #ddd;
  border-top: 0;
  vertical-align: top;
  text-align: left;
}

table td {
  padding: 10px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  vertical-align: top;
  text-align: left;
}

address {
  display: block;
  margin-bottom: 20px;
  font-style: normal;
  line-height: 20px;
}

abbr {
  text-decoration: underline;
}

abbr[title], abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted #ddd;
}

big {
  font-size: larger;
}

code {
  display: inline-block;
  padding: 2px 5px;
  background-color: #ddd;
  border: 1px solid #000;
  font-family: monospace;
  font-size: 16px;
  border-radius: 3px;
}

s,
strike,
del {
  text-decoration: line-through;
}

em,
var {
  font-style: italic;
}

ins {
  background-color: #ddd;
  text-decoration: none;
}

tt,
kbd,
samp {
  font-family: monospace;
}

pre {
  display: block;
  padding: 10px;
  margin: 0 0 20px;
  overflow-wrap: break-all;
  word-wrap: break-all;
  word-break: break-all;
  white-space: pre-wrap;
