/* Label
-------------------------------------------------------------- */
label {
  display: inline-block;
  font-weight: 700;
}

/* Checkbox + Radio
-------------------------------------------------------------- */
.checkbox--custom {
  margin-bottom: 20px;
}

.checkbox--custom input[type="checkbox"],
.checkbox--custom input[type="radio"] {
  position: absolute;
  left: -999em;
  top: -999em;
  opacity: 0;
}

.checkbox--custom input[type="checkbox"] + label,
.checkbox--custom input[type="radio"] + label {
  display: block;
  cursor: pointer;
  position: relative;
  padding-left: 30px;
}

.checkbox--custom input[type="checkbox"] + label:before,
.checkbox--custom input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 1px;
  background-color: transparent;
  width: 20px;
  height: 20px;
  margin: 0;
  border: 2px solid #fff;
  cursor: pointer;
  transition: background-color .25s ease;
}

.checkbox--custom input[type="checkbox"]:checked + label:before,
.checkbox--custom input[type="radio"]:checked + label:before {
  background-color: #fff;
}

.radio--custom {
  margin-bottom: 20px;
}

.radio--custom input[type="checkbox"],
.radio--custom input[type="radio"] {
  position: absolute;
  left: -999em;
  top: -999em;
  opacity: 0;
}

.radio--custom input[type="checkbox"] + label,
.radio--custom input[type="radio"] + label {
  display: block;
  cursor: pointer;
  position: relative;
  padding-left: 30px;
}

.radio--custom input[type="checkbox"] + label:before,
.radio--custom input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 1px;
  background-color: transparent;
  width: 20px;
  height: 20px;
  margin: 0;
