/* Menus
----------------------------------------------------------------------------------------------------*/
.menu {
  position: static;
  left: auto;
  overflow: inherit;
  background: none;
  width: 100%;
  padding: 0;
  text-align: center;
}

.menu .wrapper {
  padding: 0;
}

.menu ul {
  font-size: 18px;
}

.menu ul::after {
  clear: both;
  content: "";
  display: table;
}

.menu ul > li {
  position: relative;
  display: inline-block;
  margin: 0 15px;
  list-style-type: none;
}

.menu ul > li a {
  position: relative;
  display: block;
  padding: 0;
  text-decoration: none;
}

.menu ul > li a:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 12px;
  left: 0;
  width: 0;
  height: 1px;
  background: #000;
  animation: out 0.4s cubic-bezier(0.9, 0, 0, 1) forwards;
}

.menu ul > li a:hover, .menu ul > li a:active, .menu ul > li a:focus {
  color: #000;
}

.menu ul > li a:hover:after, .menu ul > li a:active:after, .menu ul > li a:focus:after {
  animation: in 0.4s cubic-bezier(0.9, 0, 0, 1) forwards;
}

.menu ul > li a.active {
  color: #000;
}

.menu ul > li a.active:after {
  animation: in 0.4s cubic-bezier(0.9, 0, 0, 1) forwards;
}

@keyframes in {
  from {
    width: 0;
    left: 0;
  }
  to {
    width: 100%;
    left: 0;
  }
}

@keyframes out {
  from {
    width: 100%;
    left: auto;
    right: 0;
  }
  to {
    width: 0;
    left: auto;
    right: 0;
  }
}

