/* Page Home css
-------------------------------------------------------------- */
.home {
  background: #fff;
}

.global-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
  transition: all .2s;
}

.global-wrapper.preparing:after {
  width: calc(100% - 32px);
  height: calc(100% - 32px);
  margin: 16px;
}

.global-wrapper.prepared:after {
  animation: prepared 0.7s ease-in-out forwards;
}

.global-wrapper.prepared:after .home-access {
  opacity: 0;
}

.global-wrapper.loaded {
  position: relative;
  top: auto;
  left: auto;
  overflow: visible;
}

.global-wrapper:after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  transition: all .2s;
}

@keyframes ("prepared") {
  from {
    width: calc(100% - 32px);
    margin: 16px;
  }
  to {
    width: 0;
    margin: 16px 50%;
  }
}

.home-access {
  position: relative;
  top: 50%;
  width: 270px;
  display: inline-block;
  color: #fff;
  text-align: left;
  transform: translateY(-50%);
  transition: opacity .4s;
}

@media screen and (min-width: 1024px) {
  .home-access {
    width: 380px;
  }
}

.home-access__logo {
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px;
}

.home-access__logo img {
  width: 90px;
  height: auto;
}

@media screen and (min-width: 1024px) {
  .home-access__logo img {
    width: 120px;
    height: 150px;
  }
}

.home-access__name {
  display: inline-block;
  margin: 0;
  vertical-align: middle;
  text-transform: uppercase;
  font-size: 30px;
}

@media screen and (min-width: 1024px) {
  .home-access__name {
    font-size: 45px;
  }
}

.home-access__name span {
  display: block;
}

.home-access__link {
  color: #fff;
}

.home-access__link span {
  position: relative;
}

.home-access__link span:after {
  position: absolute;
  z-index: 1;
  top: 50%;
  content: "";
  width: 100%;
  height: 2px;
  margin-top: -4px;
  background: #fff;
  animation: nameUnstroke 0.4s cubic-bezier(0.9, 0, 0, 1) forwards;
}

.home-access__link .surname:after {
  right: 0;
}

.home-access__link .name:after {
  left: 0;
