.home-access__link:hover, .home-access__link:active, .home-access__link:focus {
  color: #fff;
}

.home-access__link:hover span:after, .home-access__link:active span:after, .home-access__link:focus span:after {
  animation: nameStroke 0.4s cubic-bezier(0.9, 0, 0, 1) forwards;
}

@keyframes nameUnstroke {
  from {
    width: 100%;
  }
  to {
    width: 0%;
  }
}

@keyframes nameStroke {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

.main-content {
